<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">  </a>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">  </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">   </a>
        </li>
      </ul>

      <h1 class="content__title">  <span> 23621</span></h1>
    </div>

    <section class="cart" v-if="orderInfo">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
             &nbsp;  . &nbsp;   
            &nbsp; .    &nbsp; &nbsp;  
              .
          </p>
          <ClientForm
            :name="orderInfo.name"
            :address="orderInfo.address"
            :email="orderInfo.email"
            :phone="orderInfo.phone"
          />
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <CartBlockForm v-for="item in orderInfo.basket.items" :key="item.id" :item="item" />
          </ul>

          <CartTotalForm :total-price="orderInfo.totalPrice" :quantity="quantity" />
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import CartBlockForm from "@/components/CartBlockForm.vue";
import CartTotalForm from "@/components/CartTotalForm.vue";
import numberFormat from "@/helpers.js/numberFormat";
import ClientForm from "@/components/ClientForm.vue";
import { mapGetters } from "vuex";
import { mapState } from "vuex";
export default {
  filters: { numberFormat },
  components: { CartBlockForm, CartTotalForm, ClientForm },
  data() {
    return {
      product: null,
    };
  },

  computed: {
    ...mapState(["orderInfo"]),
    quantity() {
      return this.orderInfo.basket.items.reduce((acc, item) => item.quantity + acc, 0);
    },
  },
  created() {
    if (this.$store.state.orderInfo && this.$store.state.orderInfo.id === this.$route.params.id) {
      //       
      return;
    }
    this.$store.dispatch("loadOrderInfo", this.$route.params.id); //      
    // .then((response) => (this.product = this.$store.state.orderInfo));
  },
};
</script>
